:host {
  display: block;
  border: thin solid #bbb;
  border-radius: 5px;
  padding: 2px;
}
dc-council-card {
  height: 90px;  
}
:host(:hover) {
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
:host(.ghost) {
  background: #0f9535;
  opacity: 0.5;
}
:host(.selected) {
  border: 1px solid #0f9535;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;  
}
/* #cluster, #link {
  display: none;
} */
#type, #budget {
  font-size: 0.8rem;
  font-weight: 300;
}
#type::after {
  content: '';
}
#name {
  font-weight: 600;
  font-size: 0.9rem;
  clear:right;
}
:host(.minor.display-major) {
  display: none;
}
:host(.minor) {
  border: thin green dashed;
}
