:host {
  display: block;
  cursor: pointer;
  min-height: 75px;
  border-left: 2px solid var(--calcite-ui-brand);
  padding-left: 2px;  
}
:host(.action-agency) div  {
  opacity: 0.2;
}
:host(.action-member.editable) div  {
  border: 1px dashed green;
  background-color: lightgreen;
}

#title {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
}

#members {
  display: grid;
  grid-template-rows: repeat(auto-fill,minmax(60px,1fr));
  grid-auto-flow: rows;
  grid-auto-rows: minmax(60px,1fr);
  grid-row-gap: 5px;
}

:host(.dropzone) div:empty {
  overflow-x: auto;
  background: repeating-linear-gradient(
    45deg,
    lightgreen,
    lightgreen 10px,
    rgb(191, 244, 191) 10px,
    rgb(191, 244, 191) 20px
  )
}

